import classes from "./Paginator.module.css";
import {useState} from "react";



const Paginator = ({totalUsersCount, pageSize,currentPage, onPageChanged, portionSize = 10}) => {
    let pagesCount = Math.ceil(totalUsersCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(Math.ceil(currentPage / portionSize))
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize



    return (
        <div className = {classes.paginator}>
            {portionNumber > 1 && <button onClick={() => {setPortionNumber(portionNumber - 1)}}> PREV </button>}
                {pages
                    .filter(p => leftPortionPageNumber <= p && p <= rightPortionPageNumber)
                    .map(p => {
                    return <span className={currentPage === p ? classes.selectedPage : classes.pageLink}
                                 onClick={(e) => {
                                     onPageChanged(p)
                                 }}>{p}</span>

                })}

            {portionCount > portionNumber && <button onClick={() => {setPortionNumber(portionNumber + 1)}}> NEXT</button>}
        </div>
            )
}

export default Paginator;