import classes from "./users.module.css";
import userPhoto from "../../assets/images/user.png";
import {NavLink} from "react-router-dom";

const User = (props) => {
    return (
        <div>
                   <span>
                       <div>
                           <NavLink to={'/profile/' + props.users.id}>
                          <img className={classes.avatar}
                               src={props.users.photos.small != null ? props.users.photos.small : userPhoto}/>
                               </NavLink>
                       </div>
                       <div>
                           {props.users.followed
                               ? <button disabled={props.followingInProgress.some(id => id === props.users.id)}
                                         onClick={() => {
                                             props.unfollow(props.users.id)
                                         }}>Unfollow</button>

                               : <button disabled={props.followingInProgress.some(id => id === props.users.id)}
                                         onClick={() => {
                                             props.follow(props.users.id)
                                         }}>Follow</button>
                           }
                       </div>
                   </span>
            <span>
                        <span>
                            <div>{props.users.fullName}</div>
                             <div>{props.users.status}</div>
                        </span>
                        <span>
                             <div>{"u.location.city"}</div>
                             <div>{"u.location.country"}</div>
                        </span>
                    </span>
        </div>)
}

export default User;