const ADD_DIALOG_MESSAGE = 'dialogs/ADD-DIALOG-MESSAGE';


let initialState = {
    dialogs: [{id: 1, name: 'Sasha'},
        {id: 2, name: 'Nastya'},
        {id: 3, name: 'Vika'},
        {id: 4, name: 'Petr'},
        {id: 5, name: 'Irina'}],
    messages: [{id: 1, message: 'Hi'},
        {id: 2, message: 'Hello'},
        {id: 3, message: 'How are you'}
    ],
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_DIALOG_MESSAGE :
            return {
                ...state,
                messages: [...state.messages, {id: 4, message: action.values}],
            }
        default :
            return state;
    }

}

export const addMessageActionCreator = (values) => {
    return {
        type: ADD_DIALOG_MESSAGE, values: values
    }
}

export default dialogsReducer;