import {connect} from "react-redux";
import {
    follow, getUserThunkCreator, toggleFollowingInProgressAC, unfollow,
} from "../../redux/usersReducer";
import React from "react";
import Users from "./Users";
import Preloader from "../common/Preloader/Preloader";
import {
    getCurrentPage, getFollowingInProgress, getIsFetching, getPageSize, getTotalUsersCount, getUsersSelector
} from "../../redux/usersSelectiors";


class UsersContainer extends React.Component {
    componentDidMount = () => {
        this.props.getUserThunkCreator(this.props.currentPage, this.props.pageSize)
    }

    onPageChanged = (pageNumber) => {
        this.props.getUserThunkCreator(pageNumber, this.props.pageSize)
    }

    render = () => {
        return <div>
            {this.props.isFetching ? <Preloader/> : <Users totalUsersCount={this.props.totalUsersCount}
                                                           pageSize={this.props.pageSize}
                                                           currentPage={this.props.currentPage}
                                                           onPageChanged={this.onPageChanged}
                                                           users={this.props.users}
                                                           unfollow={this.props.unfollow}
                                                           follow={this.props.follow}
                                                           followingInProgress={this.props.followingInProgress}
                                                           toggleFollowingInProgress={this.props.toggleFollowingInProgress}
            />}
        </div>
    }
}

let mapStateToProps = (state) => {
    return {
        users: getUsersSelector(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
};
let mapDispatchToProps = (dispatch) => {
    return {
        follow: (userId) => {
            dispatch(follow(userId))
        }, unfollow: (userId) => {
            dispatch(unfollow(userId))
        }, toggleFollowingInProgress: (isFetching, userId) => {
            dispatch(toggleFollowingInProgressAC(isFetching, userId))
        }, getUserThunkCreator: (currentPage, pageSize) => {
            dispatch(getUserThunkCreator(currentPage, pageSize))
        }
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(UsersContainer);
