import classes from './Navbar.module.css';
import {NavLink} from "react-router-dom";
import {useSelector} from "react-redux";


const Navbar = () => {

    const state = useSelector((state) => state.navbar);

    let topFriendElement = state.topFriends.map((f) => {
        return (
            <div className={classes.friend}>
                <img src={f.avatar}/>
                <div>{f.name}</div>
            </div>
        )
    })

    return (
        <nav className={classes.nav}>
            <div className={classes.navItem}>
                <NavLink to="/profile"
                         className={activeLink => activeLink.isActive ? classes.active : classes.a}>Profile </NavLink>
            </div>
            <div className={classes.navItem}>
                <NavLink to="/dialogs"
                         className={activeLink => activeLink.isActive ? classes.active : classes.a}>Dialogs</NavLink>
            </div>
            <div className={classes.navItem}>
                <NavLink to="/news"
                         className={activeLink => activeLink.isActive ? classes.active : classes.a}>News</NavLink>
            </div>
            <div className={classes.navItem}>
                <NavLink to="/music"
                         className={activeLink => activeLink.isActive ? classes.active : classes.a}>Music</NavLink>
            </div>
            <div className={classes.navItem}>
                <NavLink to="/friends"
                         className={activeLink => activeLink.isActive ? classes.active : classes.a}>Friends</NavLink>
                <div className={classes.topFriends}>
                    {topFriendElement}
                </div>
            </div>
            <div className={classes.navItem}>
                <a href="src/Components/Navbar/Navbar#">Settings</a>
            </div>
        </nav>
    )
}

export default Navbar;