import {ProfileAPI} from "../API/api";

const ADD_POST = 'profile/ADD-POST';
const SET_USER_PROFILE = 'profile/SET_USER_PROFILE';
const SET_USER_STATUS = 'profile/SET_USER_STATUS';
const DELETE_POST = 'profile/DELETE_POST'


let initialState = {
    posts: [
        {id: 1, message: 'Hello', likes: '15'},
        {id: 2, message: 'It is post', likes: '1'},
        {id: 3, message: 'React', likes: '20'},
    ],
    profile: null,
    status: ""
}


const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST :
            return {
                ...state,
                posts: [...state.posts, {id: 5, message: action.value, likes: '100'}],
            }
        case SET_USER_PROFILE :
            return {
                ...state,
                profile: action.profile
            }
        case SET_USER_STATUS :
            return {
                ...state,
                status: action.status
            }
        case DELETE_POST :
            return {
                ...state, posts: state.posts.filter(p => p.id != action.postId)
            }
        default :
            return state;

    }
}

export const addPostActionCreator = (value) => ({type: ADD_POST, value: value})
export const setUserProfileAC = (profile) => ({type: SET_USER_PROFILE, profile: profile})
export const setUserStatusAC = (status) => ({type: SET_USER_STATUS, status: status})
export const deletePost = (postId) => ({type: DELETE_POST, postId: postId})


export const getUserProfile = (userId) => async (dispatch) => {
    let data = await ProfileAPI.getUserProfileById(userId)
    dispatch(setUserProfileAC(data))
}

export const getStatus = (userId) => async (dispatch) => {
    let data = await ProfileAPI.getUserStatus(userId)
    dispatch(setUserStatusAC(data))
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await ProfileAPI.updateUserStatus(status)
    if (response.data.resultCode === 0) {
        dispatch(setUserStatusAC(status))
    }


}


export default profileReducer;