import {HeaderAPI} from "../API/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';
const SET_ERROR_AUTH = 'auth/SET_ERROR_AUTH';

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    authError: null
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA :
            return {
                ...state,
                ...action.data

            }
        case SET_ERROR_AUTH :
            return {
                ...state,
                authError: action.error
            }

        default :
            return state

    }
}

export const setAuthUserDataAC = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA,
    data: {userId, email, login, isAuth}
});
export const setErrorAuthAC = (error) => ({type: SET_ERROR_AUTH, error: error});


export const authMe = () => async (dispatch) => {
    let data = await HeaderAPI.auth()
    if (data.resultCode === 0) {
        let {id, email, login} = data.data;
        dispatch(setAuthUserDataAC(id, email, login, true))
    }

}

export const login = (email, password, rememberMe) => async (dispatch) => {
    let data = await HeaderAPI.login(email, password, rememberMe)
    if (data.resultCode === 0) {
        dispatch(authMe())
    } else {
        let authError = data.messages
        dispatch(setErrorAuthAC(authError))
    }
}

export const logout = () => async (dispatch) => {
    let data = await HeaderAPI.logout()
    if (data.resultCode === 0) {
        dispatch(setAuthUserDataAC(null, null, null, false))
    }
}

export default authReducer;