import {UsersAPI} from "../API/api";

const FOLLOW = 'users/FOLLOW';
const UNFOLLOW = 'users/UNFOLLOW';
const SET_USERS = 'users/SET_USERS';
const SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'users/SET_TOTAL_USERS_COUNT';
const SET_TOGGLE_IS_FETCHING = 'users/SET_TOGGLE_IS_FETCHING';
const SET_TOGGLE_FOLLOWING_PROGRESS = 'users/SET_TOGGLE_FOLLOWING_PROGRESS';


let initialState = {
    users: [], pageSize: 10, totalUsersCount: 0, currentPage: 1, isFetching: false, followingInProgress: []
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW :
            return {
                ...state, users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    }
                    return u;
                })
            }
        case UNFOLLOW : {
            return {
                ...state, users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: false}
                    }
                    return u;
                })
            }
        }
        case SET_USERS : {
            return {...state, users: [...action.users]}
        }
        case SET_CURRENT_PAGE : {
            return {...state, currentPage: action.pageNumber}
        }
        case SET_TOTAL_USERS_COUNT : {
            return {...state, totalUsersCount: action.totalUsersCount}
        }
        case SET_TOGGLE_IS_FETCHING : {
            return {...state, isFetching: action.isFetching}
        }
        case SET_TOGGLE_FOLLOWING_PROGRESS : {
            return {
                ...state,
                followingInProgress: action.isFetching ? [...state.followingInProgress, action.userId] : state.followingInProgress.filter(id => id !== action.userId)
            }
        }


        default :
            return state

    }
}

export const followAC = (userId) => ({type: FOLLOW, userId});
export const unfollowAC = (userId) => ({type: UNFOLLOW, userId});
export const setUsersAC = (users) => ({type: SET_USERS, users});
export const setCurrentPageAC = (pageNumber) => ({type: SET_CURRENT_PAGE, pageNumber});
export const setTotalUsersCountAC = (totalUsersCount) => ({type: SET_TOTAL_USERS_COUNT, totalUsersCount});
export const toggleIsFetchingAC = (isFetching) => ({type: SET_TOGGLE_IS_FETCHING, isFetching})
export const toggleFollowingInProgressAC = (isFetching, userId) => ({
    type: SET_TOGGLE_FOLLOWING_PROGRESS, isFetching, userId
})

export const getUserThunkCreator = (currentPage, pageSize) => async (dispatch) => {
    dispatch(toggleIsFetchingAC(true))
    dispatch(setCurrentPageAC(currentPage))
    let data = await UsersAPI.getUsers(currentPage, pageSize)
    dispatch(toggleIsFetchingAC(false))
    dispatch(setUsersAC(data.items))
    dispatch(setTotalUsersCountAC(data.totalCount))
}

let followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingInProgressAC(true, userId))
    let data = await apiMethod(userId)
    if (data.resultCode === 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleFollowingInProgressAC(false, userId))
}

export const unfollow = (userId) => (dispatch) => {
    let apiMethod = UsersAPI.unfollow.bind(userId);
    let actionCreator = unfollowAC

    followUnfollowFlow(dispatch, userId, apiMethod, actionCreator)
}

export const follow = (userId) => (dispatch) => {
    let apiMethod = UsersAPI.follow.bind(userId);
    let actionCreator = followAC

    followUnfollowFlow(dispatch, userId, apiMethod, actionCreator)
}


export default usersReducer;