import * as Yup from 'yup';

export const LoginSchema = Yup.object().shape({
    email: Yup.string().email('Invalid email')
        .required('Required'),
    password: Yup.string()
        .required('Required')
});

export const AddMessageSchema = Yup.object().shape({
    message: Yup.string()
        .max(10, 'Too Long!')
})
